<template>
    <div id="fileDetail">
        <el-row>
            <el-col :span='24'>
                <el-card shadow="always">
                    <iframe v-if="show" name="myiframe" id="myrame" :src="officeOnline" frameborder="0" align="left" width="100%" height="780px" scrolling="no">
                        <p>你的浏览器不支持在线查看文件</p>
                    </iframe>
                    <div class="img">
                        <img :src="url" alt="">
                    </div>
                </el-card>
            </el-col>
        </el-row>
        <div @click="fanhui">
            <img class="icon" src="http://cdn.xqit.net/zg/img/fanhui.png"/>
        </div>
    </div>
</template>

<script>
export default {
    name: 'wjxq',
    data(){
        return{
            name: '',
            url: '',
            officeOnline: '',
            show: true,
        }
    },
    methods: {
        fanhui(){
            this.$router.go(-1)
        }
    },
    mounted() {
        console.log(this.$route.query)
        this.name = this.$route.query.fileName
        this.url = this.$route.query.fileUrl
        console.log(this.name)
        var type = this.name.split('.')[1].toLowerCase()
        if(type == 'png' || type == 'jpg' || type == 'gif' || type == 'jpeg' || type == 'bmp' || type == 'js'){
            this.show = false
            // this.url = 'https://www.baidu.com/img/baidu_jgylogo3.gif'
        }else{
            this.officeOnline = 'http://ow365.cn/?i=18218&furl=http://cdn.xqit.net/zg/file/'+this.name
            console.log(this.officeOnline)
        // this.officeOnline = 'http://ow365.cn/?i=13520&furl=' + this.url
        // https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1545810016&di=976c6fde514a7422899c855565e23712&imgtype=jpg&er=1&src=http%3A%2F%2Fwww.nycwebapps.com%2Fwp-content%2Fuploads%2F2017%2F04%2F004.png
        }
        
        
    },
}
</script>

<style>
#fileDetail{
    width: 100%;
    height: 100%;
    margin-top: 20px;
   
}
#fileDetail .el-row{
    padding: 10px;
}
#fileDetail .el-row .img{
    width: 100%;
    height: 100%;
}
#fileDetail .el-row .img img{
    width: 100%;
    height: 100%;
}
.icon{
    width: 45px;
    height: 45px;
    color: black;
    position: fixed;
    right: 0;
    bottom: 0;
    margin: 0px 130px 100px 0;
}
</style>



